import { types } from "../types/types"


export const setCloseModal=()=>{
    return {
        type:types.uiCloseModal
        
    }
}
export const setOpenModal=()=>{
    return {
        type:types.uiOpenModal
        
    }
}
export const setFinishLoading=()=>{
    return {
        type:types.uiFinishLoading
        
    }
}

export const setStartLoading=()=>{
    return {
        type:types.uiStartLoading
        
    }
}

export const setMsgLogin=(msg)=>{
    return {
        type:types.uiSetMsgLogin,
        payload:msg
        
    }
}

export const setMsgRegister=(msg)=>{
    return {
        type:types.uiSetMsgRegister,
        payload:msg
        
    }
}

