import React from 'react'
import { useDispatch } from "react-redux";
import { eventAddNew } from '../../actions/events';
import { setOpenModal } from '../../actions/ui';
import moment from 'moment';
export const AddNewFab = () => {
    const dispatch = useDispatch();
    const newEvent = {
        id: '',
        start: moment().format('yyyy-MM-DDThh:mm'),
        end: moment().add(1, 'hours').format('yyyy-MM-DDThh:mm'),
        title: '',
        notes: '',
        user: null
    }
    const handlerClick = () => {
        dispatch(eventAddNew(newEvent));
        dispatch(setOpenModal());
    }
    return (
        <>
           
            <div className="fab-container">
                <div className="fab fab-icon-holder">
                    <i className="fas fa-calendar"></i>
                </div>
                <ul className="fab-options">
                    <li>
                        <span className="fab-label">Aadir</span>
                        <div className="fab-icon-holder" onClick={handlerClick}>
                            <i className="fas fa-plus"></i>
                        </div>
                    </li>
                </ul>
            </div>
        </>
    )
}
