import { types } from "../types/types";

const initialState = {
    modalOpen: false,
    msgLogin: null,
    msgRegister: null
}

export const uiReducer = (state = initialState, action) => {
    switch (action.type) {
        case types.uiOpenModal:

            return {
                ...state,
                modalOpen: true
            };
        case types.uiCloseModal:
            return {
                ...state,
                modalOpen: false
            }
        case types.uiStartLoading:

            return {
                ...state,
                loading: true
            };
        case types.uiFinishLoading:
            return {
                ...state,
                loading: false
            }
        case types.uiSetMsgLogin:
            return {
                ...state,
                msgLogin: action.payload
            }
        case types.uiSetMsgRegister:
            return {
                ...state,
                msgRegister: action.payload
            }
        default:
            return state;
    }
}
