import { fetchConToken } from "../helpers/fetch";
import { prepareEvents, Toastmensaje } from "../helpers/funciones";
import { types } from "../types/types";
import { setCloseModal } from "./ui";

export const eventAddNew = (event) => {
    return {
        type: types.eventAddNew,
        payload: event
    }
}

export const eventSetActive = (event) => {
    return {
        type: types.eventSetActive,
        payload: event
    }
}

export const grabarEvento = () => {
    return async (dispatch, getState) => {
        const { activeEvent } = getState().cal;
        const { user } = getState().login;
        console.log(`state`, activeEvent);
        if (activeEvent.id === '') {
            //inserta
            const resp = await fetchConToken('events', activeEvent, 'POST');
            const body = await resp.json();
            console.log(`resp`, body);
            if (body.ok) {
                const eventos = await getEventos();
                dispatch(setEvents(eventos));
                dispatch(setCloseModal());
            }
        } else {
            //graba
            if (activeEvent.user._id === user.id) {
                const resp = await fetchConToken('events/' + activeEvent.id, activeEvent, 'PUT');
                const body = await resp.json();
                if (body.ok) {
                    const eventos = await getEventos();
                    dispatch(setEvents(eventos));
                    dispatch(setCloseModal());
                }
            } else {
                //mensaje de que no deje grabar un evento que no fue creado por el usuario
                Toastmensaje('No se puede modificar un evento de otro usuario', 'error');
                //return;
            }
        }
    }
}

export const eventResetActive = () => {
    return {
        type: types.eventResetActive
    }
}

export const setEvents = (events) => {
    //console.log(`events`, events);
    return {
        type: types.eventSetAllEvents,
        payload: events
    }
}


export const deleteEvent = () => {
    return async (dispatch,getState) => {
        const { activeEvent } = getState().cal;
        const { user } = getState().login;
        if (activeEvent.user._id === user.id) {
            const resp = await fetchConToken('events/' + activeEvent.id, null, 'DELETE');
            const body = await resp.json();
            if (body.ok) {
                const eventos = await getEventos();
                dispatch(setEvents(eventos));
                dispatch(setCloseModal());
                dispatch(eventResetActive());
            }
        } else {
            //mensaje de que no deje grabar un evento que no fue creado por el usuario
            Toastmensaje('No se puede eliminar un evento de otro usuario', 'error');
            //return;
        }

    }



}

export const resetEventReducer = () => {
    return {
        type: types.eventResetReducer
    }
}

export const getEventos = async () => {
    const respEvent = await fetchConToken('events', null, 'GET');
    const bodyEvent = await respEvent.json();
    const prepareEvent = prepareEvents(bodyEvent.eventos);
    //console.log(`prepareEvent`, prepareEvent);
    return prepareEvent;
}