import React from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { startLogin, startRegister } from '../../actions/login';
import { useForm } from '../customHooks/useForm';
import './LoginScreen.css';
import { setMsgRegister } from '../../actions/ui';

const loginForm={
    correo:'',
    password:''
}
const RegisterForm={
    nombre:'',
    correoRegister:'',
    passwordRegister:'',
    passwordRepet:''
}
export const LoginScreen = () => {
    const dispatch = useDispatch();
    const [values, handleInputChange, reset] = useForm(loginForm);
    const [valuesRegister, handleInputChangeRegister, resetRegister] = useForm(RegisterForm);
    const {msgLogin,msgRegister} = useSelector((state) => state.ui);
    const {correo,password}=values;
    const HandlerClick=(e)=>{
        e.preventDefault();
        dispatch(startLogin(correo,password));
    }
    const HandlerClickRegister=(e)=>{
        e.preventDefault();
        //console.log(`valuesRegister`, valuesRegister);
        if (valuesRegister.passwordRegister===valuesRegister.passwordRepet) {
            dispatch(startRegister(valuesRegister.nombre,valuesRegister.correoRegister,valuesRegister.passwordRegister));
            dispatch(setMsgRegister(null));
        } else {
            dispatch(setMsgRegister([{msg:'Los password no coinciden'}]));
        }
        
    }
 
    return (
        <div className="container login-container">
            <div className="row">
                <div className="col-md-6 login-form-1">
                    <h3>Ingreso</h3>
                    <form>
                        <div className="form-group">
                            <input 
                                type="text"
                                className="form-control"
                                placeholder="Correo"
                                name='correo'
                                onChange={handleInputChange}
                                value={correo}
                            />
                        </div>
                        <div className="form-group">
                            <input
                                type="password"
                                className="form-control"
                                placeholder="Contrasea"
                                name="password"
                                onChange={handleInputChange}
                                autoComplete="off"
                                value={password}
                            />
                        </div>
                        <div className="form-group">
                            <input 
                                type="submit"
                                className="btnSubmit"
                                value="Login" 
                                onClick={HandlerClick}
                            />
                        </div>
                    </form>
                    {
                   msgLogin &&
                   msgLogin.map((element,index)=>(
                    <div className="auth__alert-error" key={index}>
                        {element.param? element.param+': '+element.msg:element.msg}
                    </div>
                   ))
                
                    
                    }
                </div>

                <div className="col-md-6 login-form-2">
                    <h3>Registro</h3>
                    <form>
                        <div className="form-group">
                            <input
                                type="text"
                                className="form-control"
                                placeholder="Nombre"
                                name="nombre"
                                value={valuesRegister.nombre}
                                onChange={handleInputChangeRegister}
                            />
                        </div>
                        <div className="form-group">
                            <input
                                type="email"
                                className="form-control"
                                placeholder="Correo"
                                name="correoRegister"
                                onChange={handleInputChangeRegister}
                                value={values.correoRegister}
                            />
                        </div>
                        <div className="form-group">
                            <input
                                type="password"
                                className="form-control"
                                placeholder="Contrasea" 
                                autoComplete="off"
                                name="passwordRegister"
                                value={valuesRegister.passwordRegister}
                                onChange={handleInputChangeRegister}
                            />
                        </div>

                        <div className="form-group">
                            <input
                                type="password"
                                className="form-control"
                                placeholder="Repita la contrasea" 
                                autoComplete="off"
                                name="passwordRepet"
                                value={valuesRegister.passwordRepet}
                                onChange={handleInputChangeRegister}
                            />
                        </div>

                        <div className="form-group">
                            <input 
                                type="submit" 
                                className="btnSubmit" 
                                value="Crear cuenta"
                                onClick={HandlerClickRegister} />
                        </div>
                    </form>
                    {
                   msgRegister &&
                   msgRegister.map((element,index)=>(
                    <div className="auth__alert-error" key={index}>
                        {element.param? element.param+': '+element.msg:element.msg}
                    </div>
                   ))
                
                    
                    }
                </div>
            </div>
        </div>
    )
}
