import Swal from 'sweetalert2';
import moment from "moment";

export const Toastmensaje=(mensaje,logo)=> {
    const Toast = Swal.mixin({
      toast: true,
      position: 'top-end',
      showConfirmButton: false,
      timer: 2000,
      timerProgressBar: true,
      didOpen: (toast) => {
        toast.addEventListener('mouseenter', Swal.stopTimer)
        toast.addEventListener('mouseleave', Swal.resumeTimer)
      }
    });
    if (logo === 'success' || logo === 'error' || logo === 'warning' || logo === 'info' || logo === 'question') {
      Toast.fire({
        icon: logo,
        title: mensaje
      });
    }

  }

export const loadMensaje=()=>{
  Swal.fire({
    title:'Uploading...',
    text:'Please wait...',
    allowOutsideClick:false,
    showConfirmButton:false,
    didOpen:()=>{
      Swal.showLoading();
    }
  });
}

export const closeLoadMensaje=()=>{
  Swal.close();
}


export const arrayToString=(array)=>{
  let msg='';
  array.forEach(element => {
    msg=msg+'<div>El parametro '+element.param+': '+element.msg+'</div>'
  });
  return msg;
}



export const prepareEvents=(events=[])=>{

  return events.map(e=>({
    ...e,
    start:moment(e.start).toDate(),
    end:moment(e.end).toDate()
  }));
}