import React, { useState } from 'react';
import { Navbar } from '../ui/Navbar';
/*necesario esa forma para cargar calendario */
import { Calendar, momentLocalizer  } from 'react-big-calendar' 
import 'react-big-calendar/lib/css/react-big-calendar.css';
import moment from 'moment';
import 'moment/locale/es';
import { messages } from '../../helpers/calendar-messages-es';
import { CalendarEvent } from './CalendarEvent';
import { CalendarModal } from './CalendarModal';
import { useDispatch, useSelector} from "react-redux";
import { setOpenModal } from '../../actions/ui';
import { eventSetActive } from '../../actions/events';
import { AddNewFab } from '../ui/AddNewFab';

moment.locale('es');
const localizer = momentLocalizer(moment);
export const CalendarScreen = () => {
    const [lastView, setLastView] = useState(localStorage.getItem('view')||'month');
    const dispatch = useDispatch();
    const {events} = useSelector((state) => state.cal);
    const {user} = useSelector((state) => state.login);

    const eventStyleGetter=(event,start,end,isSelected)=>{
        //console.log(event);
        let colorEvento='#367Cf7'
        if (user.id!==event.user._id) {
            colorEvento='red'
        }
        const style={
            backgroundColor:colorEvento,
            borderRadius:'0px',
            opacity:0.8,
            color:'white'
        }
        
        return {style}
    }

    const onDoubleClick=(e)=>{
        //dispatch(eventSetActive(e));
    }
    const onSelectEvent=(e)=>{
        //console.log(`select`, e);
        dispatch(eventSetActive(e));
        dispatch(setOpenModal());
        
    }
    const onViewChange=(e)=>{
        //console.log(`vista`, e);
        localStorage.setItem('view',e);
        setLastView(e);
    }

    return (
        <div>
            <Navbar />
            <div>
                <Calendar
                    localizer={localizer}
                    events={events}
                    startAccessor="start"
                    endAccessor="end"
                    style={{ height: 550 }}
                    messages={messages}
                    eventPropGetter={eventStyleGetter}
                    components={{
                        event: CalendarEvent
                    }}
                    onDoubleClickEvent={onDoubleClick}
                    onView={onViewChange}
                    onSelectEvent={onSelectEvent}
                    view={lastView}
                />
                <CalendarModal/>
                <AddNewFab/>
            </div>
        </div>
    )
}
