import { fetchConToken, fetchSinToken } from "../helpers/fetch";
import { Toastmensaje } from "../helpers/funciones";
import { types } from "../types/types";
import { getEventos, resetEventReducer, setEvents } from "./events";
import { setFinishLoading, setMsgLogin, setMsgRegister, setStartLoading } from "./ui";


export const startLogin = (email, password) => {
    return async (dispatch) => {
        //console.log(email, password);
        const data = {
            email, password
        }
        const resp = await fetchSinToken('auth', data, 'POST');
        const body = await resp.json();
        //console.log(`resp`, body);    
        if (body.ok) {
            //guarda token y pasa a calendario
            localStorage.setItem('token', body.user.token);
            localStorage.setItem('token-init-date', new Date().getTime());
            dispatch(setMsgLogin(null));
            dispatch(setUser({ id: body.user.id, name: body.user.name }));
            //console.log(`resp`, body); 
            Toastmensaje(body.msg[0].msg, 'success');
            //cargar eventos
            const eventos=await getEventos();
            dispatch(setEvents(eventos));
            //console.log(`bodyEvent`, bodyEvent);
        } else {
            dispatch(setMsgLogin(body.msg));
        }
    }
}



export const startRegister = (name, email, password) => {
    return async (dispatch) => {
        //console.log(email, password);
        const data = {
            name, email, password
        }
        const resp = await fetchSinToken('auth/new', data, 'POST');
        const body = await resp.json();
        //console.log(`resp`, body);    
        if (body.ok) {
            //guarda token y pasa a calendario
            localStorage.setItem('token', body.user.token);
            localStorage.setItem('token-init-date', new Date().getTime());
            dispatch(setMsgRegister(null));
            dispatch(setUser({ id: body.user.id, name: body.user.name }));
            Toastmensaje('Usuario Creado', 'success');
            //cargar eventos
            const eventos=await getEventos();
            dispatch(setEvents(eventos));
            
        } else {
            dispatch(setMsgRegister(body.msg));
        }
    }
}


export const setUser = (user) => {
    return {
        type: types.loginSetUser,
        payload: user
    }
}

export const startChecking = () => {
    return async (dispatch) => {
        const resp = await fetchConToken('auth/renew', null, 'GET');
        const body = await resp.json();
        //console.log(`resp`, body);    
        if (body.ok) {
            //guarda token y pasa a calendario
            localStorage.setItem('token', body.user.token);
            localStorage.setItem('token-init-date', new Date().getTime());
            //dispatch(setMsgLogin(null)); 
            dispatch(setUser({ id: body.user.id, name: body.user.name }));
            const eventos=await getEventos();
            dispatch(setEvents(eventos));
        } else {
            dispatch(setUser(null));
            dispatch(checkingFinish());
            localStorage.removeItem('token');
            localStorage.removeItem('token-init-date');

        }
    }

}
const checkingFinish = () => { return { type: types.loginCheckingFinish } }

export const setLogout = () => {
    return (dispatch) => {
        localStorage.removeItem('token');
        localStorage.removeItem('token-init-date');
        dispatch(resetEventReducer());
        dispatch(setUser(null));
    }
}

