import React, { useEffect, useRef, useState } from 'react';
import Modal from 'react-modal';
import { useForm } from '../customHooks/useForm';
import moment from 'moment';
import { Toastmensaje } from '../../helpers/funciones';
import { useDispatch, useSelector } from "react-redux";
import { setCloseModal } from '../../actions/ui';
import { deleteEvent, eventSetActive, grabarEvento } from '../../actions/events';
const customStyles = {
    content: {
        top: '50%',
        left: '50%',
        right: 'auto',
        bottom: 'auto',
        marginRight: '-50%',
        transform: 'translate(-50%, -50%)',
    },
};
Modal.setAppElement('#root');
export const CalendarModal = () => {
    const dispatch = useDispatch();
    const { modalOpen } = useSelector((state) => state.ui);
    const { activeEvent } = useSelector((state) => state.cal);

    //console.log(`activeEvent`, activeEvent);
    const [error, setError] = useState('');
    const [values, handleInputChange, reset] = useForm(activeEvent);


    const activeId = useRef(activeEvent?.id);
    //console.log(`activeId`, activeId.current);
    useEffect(() => {
        if (activeEvent?.id !== activeId.current || activeId.current === '') {
            reset({
                ...activeEvent,
                start: moment(activeEvent.start).format('yyyy-MM-DDThh:mm'),
                end: moment(activeEvent.end).format('yyyy-MM-DDThh:mm')
            });
            activeId.current = activeEvent?.id;
            //console.log(`entre`, true);
        }

    }, [activeEvent]);
    //console.log(`values`, values);
    function closeModal() {
        dispatch(setCloseModal());
    }

    const HandlerClick = (e) => {
        e.preventDefault();
        //console.log(`values`, values);
        if (values.start >= values.end) {
            setError('La fecha inicial no puede ser  mayor o igual a la fecha final');
            Toastmensaje('La fecha inicial no puede ser  mayor o igual a la fecha final', 'error');
        } else if (values.title.length === 0) {
            setError("El campo de titulo debe estar lleno");
            Toastmensaje('El campo de titulo debe estar lleno', 'error');
        } else {
            setError('');
            dispatch(eventSetActive(values));
            dispatch(grabarEvento());
        }
    }
    const HandlerClickDelete = (e) => {
        e.preventDefault();
        dispatch(deleteEvent());
    }
    return (
        <Modal
            isOpen={modalOpen}
            //onAfterOpen={afterOpenModal}
            onRequestClose={closeModal}
            closeTimeoutMS={200}
            style={customStyles}
            className="modal"
            overlayClassName="modal-fondo"
        >
            <h3 style={{ textAlign: 'center' }}> Evento </h3>
            <hr />
            <form className="container">

                <div className="form-group">
                    <p>Fecha y hora inicio</p>
                    <input
                        className="form-control"
                        onChange={handleInputChange}
                        type="datetime-local"
                        value={values?.start}
                        name="start"
                    />
                </div>

                <div className="form-group">
                    <label>Fecha y hora fin</label>
                    <input
                        className="form-control"
                        type="datetime-local"
                        onChange={handleInputChange}
                        value={values?.end}
                        name="end"
                    />
                </div>

                <hr />
                <div className="form-group">
                    <label>Titulo y notas</label>
                    <input
                        type="text"
                        className="form-control"
                        placeholder="Ttulo del evento"
                        name="title"
                        onChange={handleInputChange}
                        value={values?.title}
                        autoComplete="off"
                    />
                    <small id="emailHelp" className="form-text text-muted">Una descripcin corta</small>
                </div>

                <div className="form-group">
                    <textarea
                        type="text"
                        className="form-control"
                        placeholder="Notas"
                        rows="3"
                        name="notes"
                        onChange={handleInputChange}
                        value={values?.notes}
                    ></textarea>
                    <small id="emailHelp" className="form-text text-muted">Informacin adicional</small>
                </div>
                <div className="form-group">
                    <label style={{ color: 'red' }}>{error}</label>
                </div>
                {
                    activeId.current === '' ?
                        '' :
                        (<button style={{ width: "48%", marginRight: "5px" }}
                            type="submit"
                            className="btn btn-outline-danger" onClick={HandlerClickDelete}
                        >
                            <i className="fas fa-trash-alt"></i>
                            <span> Eliminar</span>
                        </button>)
                }

                <button
                    type="submit" style={activeId.current === '' ? { width: "100%" } : { width: "50%" }}
                    className="btn btn-large btn-outline-primary " onClick={HandlerClick}
                >
                    <i className="far fa-save"></i>
                    <span> Guardar</span>
                </button>
            </form>
        </Modal>
    )
}
