import { types } from "../types/types";

const initialState = {
    user: null,
    checking:true
}

export const loginReducer = (state = initialState, action) => {
    switch (action.type) {
        case types.loginSetUser:
            return {
                ...state,
                checking:false,
                user: action.payload
            }
        case types.loginCheckingFinish:
            return {
                ...state,
                checking:false
            }    
        default:
            return state;
    }
}